.wp-block-checkout-engine-checkout-form::after {
	content: '';
	pointer-events: none;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	border-radius: 4px;
	box-shadow: 0 0 0 var( --wp-admin-border-width-focus ) transparent;
}

.block-editor-block-list__layout
	.block-editor-block-list__block:not( [contenteditable] ):focus::after,
.has-child-selected .wp-block-checkout-engine-checkout-form::after {
	position: absolute;
	z-index: 1;
	pointer-events: none;
	content: '';
	top: -20px;
	bottom: -20px;
	left: -20px;
	right: -20px;
	box-shadow: 0 0 0 var( --wp-admin-border-width-focus )
		var( --wp-admin-theme-color );
	border-radius: 4px;
}
